@import "_import";

* {
  backface-visibility: hidden;
}

$black-color-transition: all .3s ease-in;

$vhVal: 0.0925925925925926vh;
$moVwVal: 0.13vw;
// $moVwVal: 0.15vw;

html {
  height: -webkit-fill-available;
  @media (min-width: 1201px) and (min-aspect-ratio: 46/27) {
    font-size: 0.0925925925925926vh;
  }
  @media (min-width: 1201px) and (max-aspect-ratio: 46/27) {
    font-size: 0.052083333333333vw;
  }
  @media (max-width: 1200px) and (min-aspect-ratio: 46/27) {
    font-size: 0.075vh;
  }
  @media (max-width: 1200px) and (max-aspect-ratio: 46/27) {
    font-size: 0.07vw;
  }
	@include mobile {
		font-size: 0.266666666666667vw;
	}
  @include tablet {
		font-size: 0.15vw;
	}
  overflow-x: hidden;
}

body {
  min-height: 100vh;
  min-height: -webkit-fill-available;
  overflow-x: hidden;
}

#map_pc {
  canvas {
    pointer-events: none;
  }
}

.mCSB_scrollTools {
  opacity: 0 !important;
}

[class^="gsap-marker"] {
  z-index: -1;
  opacity: 0;
}

.c-header__logo-img--wh {
  display: block;
  opacity: 0;
}

.c-header {
  &__wrap {
    .c-header__keyword {
      transition: color 0.5s ease;
    }
    .c-header__menu-btn {
      transition: color 0.5s ease;
    }
    .c-header__logo-img {
      transition: opacity 0.5s ease;
    }
    &--floor-active {
      .c-header__keyword {
        color: #fff;
      }
      .c-header__menu-btn {
        color: #fff;
      }
      .c-header__logo-img {
        position: absolute;
        top: 0;
        left: 0;
      }
      .c-header__logo-img--bk {
        opacity: 0;
      }
      .c-header__logo-img--wh {
        opacity: 1;
      }
    }
  }
}

.c-footer {
  display: none;
  @include mobile {
    display: block;
  }
}

.top {
  right: 22rem;
  bottom: 54rem;
  @include mobile {
    right: 14rem;
    bottom: 15rem;
  }
}

.quick {
  $self: &;
  position: fixed;
  left: 60rem;
  bottom: 30rem;
  z-index: 110;
  @include mobile {
    bottom: 20rem;
    left: 16rem;
  }
  &--active {
    display: flex;
    align-items: center;
    position: fixed;
    left: 0;
    bottom: 0;
    padding: 30rem 60rem;
    width: 100%;
    background: #124734;
    @include mobile {
      padding: 0;
    }
    #{$self}__list {
      display: inline-flex;
      align-items: center;
      @include mobile {
        display: flex;
        align-items: stretch;
        flex-direction: column-reverse;
        padding: 29rem 41rem 117rem 34rem;
      }
    }
    #{$self}__map-btn {
      display: block;
    }
    #{$self}__all-btn {
      &--open {
        display: none;
      }
      &--close {
        display: inline-block;
      }
    }
  }
  &__all-btn {
    width: 100rem;
    height: 100rem;
    font-family: "ambit-semi";
    font-weight: 400;
    font-size: 40rem;
    color: #fff;
    background-color: #124734;
    border-radius: 80rem;
    box-shadow: 0px 10px 12px rgba(0, 0, 0, 0.2);
    @include mobile {
      width: 60rem;
      height: 60rem;
      font-size: 21rem;
    }
    @at-root .br-ie & {
      line-height: 113rem;
      @include mobile {
        line-height: 66rem;
      }
    }
    @at-root .br-win-ch & {
      line-height: 106rem;
      @include mobile {
        line-height: 66rem;
      }
    }
    &--close {
      display: none;
      font-family: "ambit-semi";
      border: 3px solid #fff;
      background: url("/images/btn_floor_close.png") no-repeat 50% 50%/24rem;
      color: transparent;
      text-indent: -999999px;
      box-shadow: none;
      @include mobile {
        position: absolute;
        bottom: 20rem;
        left: 16rem;
        z-index: 10;
        border-width: 2px;
        background: url("/images/btn_floor_close.png") no-repeat 50% 50%/14rem;
      }
    }
  }
  &__list {
    display: none;
    padding: 0 0 0 40rem;
    @include mobile {
      padding: 0 40rem 0 35rem;
      width: 100%;
    }
    @include ie-only {
      padding: 11rem 0 0 40rem;
    }
  }
  &__itm {
    margin: 0 29rem 0 0;
    &:first-of-type {
      margin: 0 35rem 0 0;
    }
    @include mobile {
      &:nth-of-type(n) {
        margin: 0 0 11rem;
      }
      &:first-child {
        margin: 8rem 0 -10rem;
        #{$self}__tit {
          font-family: "roslindaletext";
          @at-root .br-win-ch {
            position: relative;
            top: -2rem;
          }
        }
      }
    }
  }
  &__floor-btn {
    @include mobile {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  &__tit {
    font-family: "roslindaletext";
    font-size: 32rem;
    color: #fff;
    @include mobile {
      font-family: "ambit-semi";
      font-size: 20rem;
      color: #fff;
      letter-spacing: -0.2rem;
    }
  }
  &__tit-all {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      bottom: -6rem;
      left: 0;
      width: 100%;
      height: 3rem;
      background-color: #fff;
    }
    @include mobile {
      &:after {
        height: 1.5rem;
      }
    }
    @at-root .br-ie & {
      position: relative;
      top: -6px;
    }
    @at-root .br-win-ch & {
      position: relative;
      top: -2px;
    }
  }
  &__num {
    font-family: "ambit-semi";
    font-size: 32rem;
    color: #fff;
    @include mobile {
      margin: -2rem 0 0 0;
      font-size: 14rem;
    }
  }
  &__txt {
    display: none;
    @include mobile {
      display: inline;
    }
  }
  &__floor-special {
    font-family: "ambit-semi";
  }
  &__map-btn {
    display: none;
    position: absolute;
    right: 105rem;
    bottom: 49rem;
    padding: 11rem 25rem;
    border: 3rem solid #fff;
    border-radius: 32rem;
    font-family: "Noto Sans KR Medium";
    font-size: 24rem;
    color: #fff;
    @include mobile {
      bottom: 36rem;
      right: 16rem;
      padding: 6rem 15rem;
      border-width: 1px;
      font-size: 12rem;
      letter-spacing: -0.6rem;
    }
    @at-root .br-win-ch &{
      padding: 9rem 25rem 11rem;
    }
  }
}

.vi {
  position: fixed;
  top: 0;
  left: 50%;
  width: calc(100vw + 200px);
  height: 100vh;
  transform: translate(-50%, 0);
  @include mobile {
    top: auto;
    left: auto;
    bottom: 0;
    min-width: 0;
    width: 100%;
    height: auto;
    transform: none;
  }
  &__img {
    height: 100%;
    object-fit: cover;
    @include ie-only {

    }
    @include mobile {
      width: 100%;
      height: auto;
    }
  }
}

.floor {
  $self: &;
  position: relative;
  &--info {
    ~ .c-footer {
      display: none;
    }
  }
  &--b2 {
    .brd__itm--1 {
      .brd__info {
        color: #000;
      }
    }
  }
  &--2 {
    .brd__itm {
      .brd__info {
        color: #000;
      }
    }
  }
  &--3 {
    .brd__itm--1 {
      .brd__info {
        color: #000;
      }
    }
  }
  &--5 {
    .brd__itm--2 {
      .brd__info {
        @include mobile {
          color: #000;
        }
      }
    }
  }
  &--6 {
    .brd__itm--1 {
      .brd__info {
        color: #000;
      }
    }
    + footer {
      @include mobile {
        display: block;
      }
    }
  }
  &__wrp {
    display: inline-flex;
    flex-wrap: nowrap;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    // height: 0 !important;
    // will-change: transform;
    // perspective: 1000;
    @include ie-only {
      display: inline-block;
      white-space: nowrap;
    }
    @include mobile {
      display: block;
      position: relative;
      // will-change: auto;
      perspective: none;
    }
  }
  &__otr {
    flex: 0 0 auto;
    @include ie-only {
      display: inline-block;
      vertical-align: middle;
    };
    @include mobile {
      display: block;
      flex: none;
      &:last-of-type {
        margin: 0;
      }
    }
    #{$self}__sec--visual, #{$self}__sec--brand {
      img {
        display: none;
        @include mobile {
          display: block;
        }
      }
    }
    &--res-load {
      #{$self}__sec--visual, #{$self}__sec--brand {
        img {
          display: block;
        }
      }
    }
  }
  &__inr {
    display: inline-flex;
    flex-wrap: nowrap;
    @include ie-only {
      display: inline-block;
      vertical-align: middle;
    }
    @include mobile {
      display: block;
    }
  }
  &__sec {
    flex: 0 0 auto;
    height: 100vh;
    overflow: hidden;
    @include mobile {
      flex: none;
      height: auto;
    }
    @include ie-only {
      display: inline-block;
      vertical-align: middle;
    }
    &--info {
      position: relative;
      padding: (213 * $vhVal) (59 * $vhVal) (60 * $vhVal) (59 * $vhVal);
      min-width: 100vw;
      border-right: 1px solid #000;
      background-color: #fff;
      height: 100%;
      @include mobile {
        padding: 125rem 15rem 50rem;
        min-width: 0;
        height: 100vh;
        border-right: 0;
      }
    }
    &--cover {
      min-width: 100vw;
      height: 100vh;
      background-color: #fff;
      @include mobile {
        min-width: 0;
        // height: auto;
      }
    }
    &--visual {
      min-width: 3200rem;
      width: 180vw;
      @include mobile {
        min-width: 0;
        width: auto;
        // height: 100vh;
      }
    }
    &--tag {
      min-width: 100vw;
      background-color: #fff;
      @include mobile {
        min-width: 0;
      }
    }
    &--brand {
      padding: 87 * $vhVal 0 0 0;
      min-width: 100vw;
      background-color: #fff;
      @include mobile {
        padding: 0;
        min-width: 0;
      }
    }
    &--map {
      min-width: 100vw;
      position: relative;
      background-color: #000;
      @include mobile {
        min-width: 0;
        // &.floor__sec--black {
        //   background-color: #fff;
        // }
        // transform: $black-color-transition;
      }
    }
  }
  &-info {
    $self: &;
    &__list {
      position: absolute;
      bottom: 32rem;
      left: 57rem;
      z-index: 10;
      width: calc(100% - 118rem);
      height: auto;
      .motion-txt {
        @include text-effect('.txt-target');
        .txt-target {
          display: flex;
        }
      }
      @include mobile {
        position: absolute;
        bottom: 34rem;
        left: 0;
        padding: 0 15rem;
        width: 100%;
      }
    }
    &__itm {
      padding: 0 0 22rem;
      &:last-of-type {
        padding: 0;
      }
      @include ie-only {
        padding: 2rem 0 12rem;
      }
      @include mobile {
        padding: 0 0 10rem;
        &:last-of-type {
          padding: 0;
        }
      }
      &:hover {
        #{$self}__tit {
          font-weight: 500;
          color: #124734;
          @include mobile {
            color: inherit;
          }
        }
        #{$self}__num {
          color: #124734;
          @include mobile {
            color: inherit;
          }
        }
        #{$self}__img-box {
          margin: -17rem 0 0 14rem;
          width: 352rem;
        }
        #{$self}__img-box {
          width: 352rem;
        }
      }
    }
    &__lnk {
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: relative;
      z-index: 10;
    }
    &__tit {
      display: flex;
      font-family: "ambit-semi";
      font-size: 82rem;
      letter-spacing: -0.8rem;
      color: #000;
      .img-box {
        @include img-effect;
        &:after {
          transition: width 1s 0.1s cubic-bezier(0.19, 1, 0.22, 1)
        }
        &.img-hello:after{
          width: 0;
          height: 100%;
        }
      }
      @include mobile {
        font-size: 30rem;
      }
    }
    &__special {
      display: inline-block;
      margin: -2rem 4rem 0 15rem;
      font-family: "ambit-semi";
      @include ie-only {
        position: relative;
        top: 14rem;
      }
      @include mobile {
        position: relative;
        top: 0;
        margin: 0rem 8rem 0 -1rem;
        @at-root .br-safari & {
          top: 0;
        }
        @at-root .br-safari #{$self}__itm:hover & {
          top: 0;
        }
      }
    }
    &__img-box {
      position: relative;
      width: 0;
      margin: 0;
      overflow: hidden;
      // width: 352rem;
      transition: width 0.5s ease-out;
      @include mobile {
        display: none;
      }
    }
    &__img {
      position: absolute;
      top: 0;
      left: 50%;
      width: auto;
      height: 100%;
      transform: translate(-50%, 0);
    }
    &__txt {
      &:first-of-type {
        margin: 0 4rem 0 0;
        @include mobile {
          margin: 0 8rem 0 0;
        }
      }
      &--1 {
        font-family: "roslindaletext";
        font-size: 78rem;
        letter-spacing: -0.4rem;
        @include mobile {
          font-size: 30rem;
          letter-spacing: 0;
        }
      }
      &--2 {
        margin: 0 0 0 17rem;
        font-family: "ambit-semi";
        letter-spacing: -2rem;
        @include mobile {
          margin: 0;
          letter-spacing: 0;
        }
        @include ie-only {
          position: relative;
          top: 12rem;
        }
      }
    }
    &__num {
      margin: 1rem 0 0;
      font-family: "ambit-semi";
      font-weight: 400;
      font-size: 42rem;
      color: #000;
      @include mobile {
        margin: 0 2rem 0 0;
        font-size: 18rem;
      }
    }
    &__bg-wrp {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
    }
    &__bg-box {
      opacity: 0;
      position: absolute;
      @include mobile {
        display: none;
      }
      &--active {
        opacity: 1;
      }
      &--1 {
        right: 197rem;
        bottom: 161rem;
        width: 846rem;
      }
      &--2 {
        right: 210rem;
        bottom: 107rem;
        width: 659rem;
      }
      &--3 {
        right: 198rem;
        bottom: 157rem;
        width: 770rem;
      }
      &--4 {
        right: 119rem;
        bottom: 146rem;
        width: 821rem;
      }
      &--5 {
        right: 238rem;
        bottom: 114rem;
        width: 608rem;
      }
      &--6 {
        right: 199rem;
        bottom: 143rem;
        width: 845rem;
        @include mobile {
          display: block;
          right: 14rem;
          bottom: 284rem;
          width: 296rem;
          opacity: 1;
        }
        @include tablet {
          bottom: 280rem;
          width: 588rem;
        }
      }
      &--7 {
        right: 240rem;
        bottom: 100rem;
        width: 597rem;
      }
      &--8 {
        right: 253rem;
        bottom: 177rem;
        width: 742rem;
      }
    }
    &__bg-img {
      width: auto;
      max-width: 100%;
    }
  }
  &__con {
    display: none;
    &--active {
      display: block;
    }
  }
  &__pre {
    $self: &;
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #fff;
    transform: translate(0, 100%);
    transition: transform 0.05s ease;
    @include mobile {
      position: relative;
      transform: none;
    }
    &--active{
      transform: translate(0, 0);
    }
    &-wrap {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 46rem 0;
      @include mobile {
        height: 60rem;
        padding: 0;
      }
    }
    &-tit {
      display: block;
      position: relative;
      font-family: "ambit-semi";
      font-size: 68rem;
      text-align: center;
      transition: color 0.15s ease;
      @include mobile {
        margin: 0 0 0 -13rem;
        font-size: 20rem;
        letter-spacing: 0.6rem;
      }
      @include ie-only {
        top: 10rem;
      }
    }
    &-txt {
      font-family: "roslindaletext";
      font-size: 68rem;
      @include mobile {
        font-size: 21rem;
        letter-spacing: -0.5rem;
      }
    }
    &-floor {
      position: absolute;
      top: -4rem;
      right: -54rem;
      font-family: "ambit-semi";
      font-size: 38rem;
      transition: color 0.15s ease;
      @include mobile {
        top: -2rem;
        right: -19rem;
        font-size: 12rem;
      }
      @include ie-only {
        top: -7rem;
      }
    }
    &-arrow {
      position: absolute;
      top: calc(50% + 3rem);
      right: 262rem;
      transform: translate(0, -50%);
      width: 62rem;
      height: 58rem;
      background-image: url(/images/btn_floor_arrow.png);
      background-repeat: no-repeat;
      background-size: cover;
      @include mobile {
        top: calc(50% + -1rem);
        right: 53rem;
        background-image: url(/images/btn_floor_arrow_mo.png);
        width: 17rem;
        height: 16rem;
      }
    }
    &--active {
      bottom: 0;
    }
    &:hover {
      #{$self}-tit {
        color: #124734;
        @include mobile {
          color: inherit;
        }
      }
      #{$self}-floor {
        color: #124734;
        @include mobile {
          color: inherit;
        }
      }
    }
  }
}

.cloned {
  &__wrp {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -10;
    width: 100%;
  }
  &__itm {
    border: 3px solid blue;
  }
}

.cover {
  position: relative;
  height: 100vh;
  background-color: #fff;
  @include mobile {
    padding: 83rem 20rem 0 15rem;
  }
  &__itr {
    position: relative;
  }
  &__tit {
    position: absolute;
    top: 183rem;
    left: 54rem;
    font-family: "ambit-semi";
    font-size: 82rem;
    line-height: 0.9;
    letter-spacing: -0.2rem;
    @include mobile {
      position: relative;
      top: auto;
      left: auto;
      padding: 0 0 20rem;
      font-size: 43rem;
      line-height: 0.97;
    }
  }
  &__tit-word {
    font-family: "roslindaletext";
    font-size: 78rem;
    @include mobile {
      font-size: 40rem;
    }
  }
  &__txt {
    position: absolute;
    top: 290rem;
    left: 62rem;
    font-family: "Noto Sans KR Medium";
    font-size: 24rem;
    line-height: 1.5;
    @include mobile {
      position: relative;
      top: auto;
      left: auto;
      padding: 0 0 27rem 3rem;
      font-size: 17rem;
      line-height: 1.45;
    }
  }
  &__img-box {
    position: absolute;
    top: 451rem;
    left: 59rem;
    width: 240rem;
    @include mobile {
      position: relative;
      top: auto;
      left: 2rem;
      padding: 0 0 416rem 1rem;
      width: 87rem;
    }
    &--dif {
      top: 415rem;
      @include mobile {
        top: auto;
      }
    }
  }
  .motion-txt {
    @include text-effect('.txt-target')
  }
  &__floor-box {
    position: absolute;
    bottom: -36rem;
    right: 61rem;
    display: block;
    @include mobile {
      bottom: 0;
      right: 15rem;
    }
    @include ie-only {
      bottom: 0;
      height: 500rem;
    }
  }
  &__floor {
    padding: 0 7rem 0 0;
    font-family: "ambit-semi";
    font-size: 580rem;
    text-align: right;
    letter-spacing: -34rem;
    color: #000;
    @include mobile {
      padding: 0;
      font-size: 180rem;
      letter-spacing: -7rem;
    }
    @include ie-only {
      line-height: 1.1;
    }
  }
  &--cloned {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    z-index: 20;
    border: 20px solid red;
  }
}

